/**
 * Keeps track of seed and handles pseudorandomness
 * Instance created in BaseRandomLayer class
 * Utilized for random preprocessing layers
 */
class RandomSeed {
  constructor(seed) {
    this.seed = seed;
  }
  next() {
    if (this.seed === undefined) {
      return undefined;
    }
    return this.seed++;
  }
}
RandomSeed.className = 'RandomSeed';
export { RandomSeed };
