import { IsFinite } from '@tensorflow/tfjs-core';
import { unaryKernelFunc } from '../utils/unary_utils';
export const isFinite = unaryKernelFunc(IsFinite, xi => Number.isFinite(xi) ? 1 : 0, 'bool');
export const isFiniteConfig = {
  kernelName: IsFinite,
  backendName: 'cpu',
  kernelFunc: isFinite
};
